<script>import { selectAll } from "../helpers/selection";
import IconButton from "./IconButton.svelte";
import DataSample from "./DataSample.svelte";
import PaginationControls from "./PaginationControls.svelte";
import Fa from "svelte-fa";
import { faCheckSquare } from "@fortawesome/free-solid-svg-icons/faCheckSquare";
export let title;
export let filteredTable;
export let selected;
export let canvasSpec;
export let tooltip;
let page = 0;
function resetPage() {
    page = 0;
}
$: {
    filteredTable;
    resetPage();
}
$: dataPoints = filteredTable
    .slice(page * $canvasSpec.instancesPerPage, Math.min(page * $canvasSpec.instancesPerPage + $canvasSpec.instancesPerPage, filteredTable.size))
    .objects();
</script>

<div class="w-full border rounded-md border-midgrey p-2">
  <div
    class="flex items-center mb-2 justify-between border-b pb-1 border-midgrey"
  >
    <h3 class="font-bold whitespace-nowrap mr-3">
      {title}
    </h3>
    <IconButton
      uiChange={true}
      on:click={() => {
        selectAll(
          selected,
          dataPoints.map((d) => d[$canvasSpec.idColumn])
        );
      }}
    >
      <Fa icon={faCheckSquare} slot="icon" />
    </IconButton>
  </div>
  <div
    class="flex flex-nowrap items-top border-b border-midgrey justify-between"
  >
    <div class="flex flex-wrap content-top">
      {#each dataPoints as dataPoint}
        <DataSample {dataPoint} {canvasSpec} {selected} {tooltip} />
      {/each}
    </div>
    <slot name="extra" />
  </div>
  <PaginationControls
    bind:page
    size={filteredTable.size}
    instancesPerPage={$canvasSpec.instancesPerPage}
  />
</div>
